<template>
  <v-textarea
    v-model="valueModel"
    :auto-grow="true"
    rows="1"
    :label="label"
    @keyup="onKeyUp"
    validate-on-blur
  ></v-textarea>
</template>

<script>
export default {
  name: 'TextareaField',
  props: {
    label: {
      required: true
    },
    value: {
      default: ''
    },
    id: {
      default: null
    }
  },
  data: () => ({
    valueModel: ''
  }),
  watch: {
    value (newValue) {
      this.valueModel = newValue
    }
  },
  methods: {
    onKeyUp () {
      this.$emit('onKeyUp', this.valueModel, this.id)
    }
  }
}
</script>