<template>
  <v-btn
    class="primary text-capitalize font-weight-regular"
    :class="cssClass"
    height="auto"
    :block="block"
    :disabled="disabled"
    @click="clicked"
  >
    <v-icon
      v-if="icon"
      class="mr-1"
    >
      {{ icon }}
    </v-icon>
    {{ text }}
  </v-btn>
</template>

<script>
export default {
  name: 'BtnSecondary',
  props: {
    block: {
      default: true
    },
    disabled: {
      default: false
    },
    text: {
      default: 'Confirmar'
    },
    icon: {
      default: ''
    },
    cssClass: {
      default: 'py-3'
    }
  },
  methods: {
    clicked () {
      this.$emit('clicked')
    }
  }
}
</script>