<template>
  <v-text-field
    :ref="id"
    :id="id"
    v-model="valueModel"
    :value="valueModel"
    :prepend-inner-icon="icon"
    :label="label"
    :disabled="disabled"
    :rules="rules"
    :counter="counter"
    :hint="placeholder"
    :type="type"
    v-on:keyup.enter="onKeyUpEnter"
    @keyup="onKeyUp"
    validate-on-blur
    aria-autocomplete="false"
    required
  ></v-text-field>
</template>

<script>
export default {
  name: 'InputField',
  props: {
    label: { required: true },
    value: { default: '' },
    disabled: { default: false },
    counter: { default: null },
    rules: { default: Array },
    id: { default: null },
    placeholder: { default: '' },
    type: { default: null },
    icon: { default: null }
  },
  data: () => ({
    valueModel: ''
  }),
  mounted () {
    this.valueModel = this.value
  },
  watch: {
    value (newValue) {
      this.valueModel = newValue
    }
  },
  methods: {
    onKeyUp () {
      this.$emit('onKeyUp', this.valueModel, this.id)
    },
    onKeyUpEnter () {
      this.$emit('onKeyUpEnter', this.valueModel, this.id)
    }
  }
}
</script>